.footer { background: var(--brand-dark); color: var(--sand); padding: var(--space-xl) 0 var(--space-md); }
.footer__grid {
  display: grid; grid-template-columns: 1.5fr 1fr 1fr 1fr;
  gap: var(--space-lg); margin-bottom: var(--space-lg);
}
.footer__logo { height: 44px; margin-bottom: 1rem; filter: brightness(0) invert(1); }
.footer__tagline { font-size: 0.92rem; color: rgba(240,235,228,0.6); line-height: 1.7; }
.footer__heading {
  font-size: 0.78rem; font-weight: 800; letter-spacing: 0.12em;
  text-transform: uppercase; color: var(--white); margin-bottom: 1.25rem;
}
.footer__links { display: flex; flex-direction: column; gap: 0.65rem; }
.footer__link {
  display: flex; align-items: center; font-size: 0.88rem;
  color: rgba(240,235,228,0.55); transition: color var(--transition);
}
.footer__link:hover { color: var(--white); }
.footer__social { display: flex; gap: 0.75rem; }
.footer__social-link {
  width: 38px; height: 38px; border-radius: 50%;
  border: 1px solid rgba(240,235,228,0.15);
  display: flex; align-items: center; justify-content: center;
  font-size: 1rem; color: rgba(240,235,228,0.55);
  transition: all var(--transition);
}
.footer__social-link:hover { background: var(--brand); border-color: var(--brand); color: var(--white); }
.footer__bottom {
  border-top: 1px solid rgba(240,235,228,0.08); padding-top: var(--space-md);
  text-align: center; font-size: 0.78rem; color: rgba(240,235,228,0.3);
}
/* Newsletter */
.footer__newsletter-text {
  font-size: 0.88rem; color: rgba(240,235,228,0.55); line-height: 1.6; margin-bottom: 1rem;
}
.footer__newsletter-form {
  display: flex; gap: 0.5rem;
}
.footer__newsletter-input {
  flex: 1; padding: 0.65rem 0.85rem; font-size: 0.88rem;
  background: rgba(255,255,255,0.08); border: 1px solid rgba(255,255,255,0.12);
  border-radius: 8px; color: var(--white); font-family: var(--font-body);
}
.footer__newsletter-input::placeholder { color: rgba(240,235,228,0.35); }
.footer__newsletter-input:focus { outline: none; border-color: var(--brand); }
.footer__newsletter-btn {
  padding: 0.65rem 1.25rem; font-size: 0.75rem; font-weight: 800;
  letter-spacing: 0.06em; text-transform: uppercase;
  background: var(--brand); color: var(--white); border-radius: 8px;
  transition: all var(--transition); white-space: nowrap;
}
.footer__newsletter-btn:hover { background: #4d8a7a; transform: translateY(-1px); }
.footer__newsletter-thanks {
  font-size: 0.92rem; color: var(--brand); font-weight: 600;
}

.footer__link { text-decoration: none; }

@media (max-width: 768px) { .footer__grid { grid-template-columns: 1fr 1fr; gap: var(--space-md); } }
@media (max-width: 480px) {
  .footer__grid { grid-template-columns: 1fr; }
  .footer__newsletter-form { flex-direction: column; }
}
